.loaderLine-container {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    width: 100vw;
    height: 100vh;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.loaderLine {
    width: 100vw;
    height: 1px;
    /* background-image: linear-gradient(90deg, #3498db, #2ecc71); */
    background-image: -webkit-linear-gradient(30deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%);
    background-image: -o-linear-gradient(30deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%);
    background-image: linear-gradient(60deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%);
    -webkit-animation: loaderLine 2.0s ease-out;
            animation: loaderLine 2.0s ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; 
}

@-webkit-keyframes loaderLine {
    0% {
        -webkit-transform: scale3d(1, 1, 1);
                transform: scale3d(1, 1, 1);
    }

    100% {
        -webkit-transform: scale3d(0, 1, 1);
                transform: scale3d(0, 1, 1);
        /* width: 1px; */
        /* height: 1px; */
    }
}

@keyframes loaderLine {
    0% {
        -webkit-transform: scale3d(1, 1, 1);
                transform: scale3d(1, 1, 1);
    }

    100% {
        -webkit-transform: scale3d(0, 1, 1);
                transform: scale3d(0, 1, 1);
        /* width: 1px; */
        /* height: 1px; */
    }
}


