.app {
  width: 100%;
  min-height: 100vh;
  position: absolute;
  overflow: hidden;
  background-image: -webkit-linear-gradient(30deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%);
  background-image: -o-linear-gradient(30deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%);
  background-image: linear-gradient(60deg, #3d3393 0%, #2b76b9 37%, #2cacd1 65%, #35eb93 100%);
}

.appContainer {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: -webkit-gradient(linear, left bottom, left top, color-stop(50%, #011222),to(#1c2935));
  background: -webkit-linear-gradient(bottom, #011222 50%,#1c2935 100%);
  background: -o-linear-gradient(bottom, #011222 50%,#1c2935 100%);
  background: linear-gradient(to top, #011222 50%,#1c2935 100%);
  -webkit-transition: opacity 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  -o-transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
  outline: 1px solid #000;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  /* opacity: 0.75; */
}

.appContainer--inActive {
  /* opacity: 0.75; */
}


